
































.detail-field {
    flex-direction: row;
    padding-left: 0 !important;
}
.detail-field .field-label {
    width: 50px;
}
.detail-field .field-value {
    flex: 1;
}
.holiday-item-container {
    width: 100%;
}
.holiday-item {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.reason {
    overflow: auto;
    overflow-wrap: break-word;
    hyphens: auto;
    flex: 1 1 auto;
}
