.page-heading,
.page-contents {
  display: grid; }

.page-heading {
  grid-template-columns: 2fr 1fr;
  grid-template-areas: "heading-caption heading-stat-1";
  grid-column-gap: 1rem; }

.heading-caption {
  grid-area: heading-caption; }

.heading-stat-1 {
  grid-area: heading-stat-1; }

.page-contents {
  grid-template-columns: repeat(auto-fit, 360px);
  grid-column-gap: 1rem;
  grid-row-gap: 2rem; }

div[class*="content-"] > div {
  min-height: 4em;
  justify-content: left;
  background: rgba(128, 128, 128, 0.35);
  margin: 1px 0;
  width: 100%; }

div[class*="content-"] > div:nth-child(2) {
  background: rgba(128, 128, 128, 0.55); }

.ml-auto {
  margin-left: auto; }

.caption-icon {
  font-size: 2.5rem;
  display: inline-flex; }

.date {
  width: 100%;
  background: none; }

@media (max-width: 400px) {
  .page-heading {
    grid-template-columns: 1fr;
    grid-template-areas: "heading-caption" "heading-stat-1"; }
  .page-contents {
    grid-template-columns: 1fr; } }

@media (min-width: 1820px) {
  .page-heading {
    grid-template-columns: repeat(4, 494px);
    grid-template-areas: "heading-caption heading-caption heading-stat-1 ."; } }
